@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
.tasks {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.task {
    background: white;
    color: white;
    padding-top: 2rem;
    border-radius: 1.3rem;
    width: 200px;
    font-family: 'Rubik';
    margin: 1rem;
    box-shadow: 0px 0px 3px black;
}

.task-category {
    padding: .3rem;
    background-color: orchid;
    display: inline;
    border-radius: .5rem;
    font-weight: 300;
}

.task {
    background-repeat: no-repeat;
    background-position: top;
}

.task-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.option-icon {
    cursor: pointer;
}

.options-list {
    position: absolute;
    background-color: var(--primary-color);
    padding: .8rem;
    right: 0;
    visibility: hidden;
}

.options-list.active {
    border-radius: .3rem;
    visibility: visible;
}

.task-option {
    cursor: pointer;
}

.task-body {
    background: var(--secondary-color);
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 1rem;
    border-radius: 1rem;
}

.task-title {
    line-break: anywhere;
    margin-bottom: .3rem;
}

.task-state {
    display: inline-block;
    padding: .2rem;
    margin-top: .3rem;
    border-radius: .5rem;
}

.task-content {
    margin: 1rem 0;
}

.time-remaining {
    font-size: 4rem;
}

/* Task colors*/
.task-state.scheduled {
    background: rgba(56, 159, 255, 0.47);
}

.task-state.active {
    background: rgba(87, 255, 56, 0.47);
}

.task-state.complete {
    background: rgba(255, 56, 56, 0.47);
}

.task.work,.task.work .task-category {
    background-color: orange;
    background-image: url(../../static/media/icon-work.a3f124e5.svg);
}

.task.play,.task.play .task-category {
    background-color: blue;
    background-image: url(../../static/media/icon-play.e8d4e569.svg);
}

.task.study,.task.study .task-category {
    background-color: red;
    background-image: url(../../static/media/icon-study.1f0a65e4.svg);
}

.task.selfCare,.task.selfCare .task-category {
    background-color: yellow;
    background-image: url(../../static/media/icon-self-care.a161aff5.svg);
}
.dashboard {
    width: 100%;
}

.taskboard {
    margin: 0.5rem;
    max-width: 1375px;
    margin: 0 auto;
}

.taskboard::-webkit-scrollbar {
    width: .6rem;
}

.taskboard::-webkit-scrollbar-track {
    background-color: var(--body-color);
}

.taskboard::-webkit-scrollbar-thumb {
    background-color: white;
    border: 2px solid white;
    border-radius: .5rem;
}

@media screen and (min-width: 968px) {
    .tasks {
        flex-direction: row;
    }
}

@media screen and (min-width: 600px) {
    .taskboard {
        overflow-y: auto;
        height: 31rem;
    }
}
.user {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--primary-color);
    border-radius: 1rem;
    padding: 2.6rem;
}

.user-image {
    background-size: cover;
    background-position: center;
    width: 6rem;
    height: 6rem;
    border-radius: 3rem;
    border: 4px solid white;
    display: inline-block;
    transition: 0.5s;
}
.user-image:hover {
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    cursor: pointer;
    transition: 0.5s;
}

.image-upload {
    display: none;
}

.user-text {
    padding: .3rem;
    color: white;
    font-size: .7rem;
}

.username {
    width: 180px;
    font-weight: 100;
}
.dashboard-filter {
    color: white;
    font-family: 'Gemunu Libre';
    font-size: 1.2rem;
    font-weight: 700;
}

.filter-item {
    font-family: 'Roboto';
    font-size: 1rem;
    font-weight: 400;
    margin: .2rem .4rem;
}
.modal {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 1;
}

.task-modal {
    display: flex;
}

.modal-close {
    cursor: pointer;
    position: absolute;
    top: 17px;
    right: 25px;
    font-size: 2rem;
    color: white;
}

.taskmodal-body {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;

    background: var(--primary-color);
    box-shadow: 0px 0px 6px;
    border: 4px solid white;
    border-radius: 24px;
    overflow-y: auto;

    width: 70vw;
    min-width: 240px;
    max-width: 320px;
    max-height: 85vh;
}

.taskmodal-form {
    margin: auto;
}

.task-form > * {
    margin: 1rem 0;
}

.task-form {
    color: white;
    font-family: 'Rubik';
}

.task-form input, select {
    display: block;
    width: 100%;
    border: none;
    border-bottom: 2px solid orange;
    border-radius: 0.2rem;
}

.submit-button {
    border-style: solid;
    border-radius: .5rem;
    background-color: orange;
    padding: .5rem;
    width: 100%;
}

@media screen and (min-width: 600px) {
    .modal {
        height: auto;
    }
    .taskmodal-body {
        flex-direction: row;
        padding: 30px;
        width: auto;
        max-width: 539px;
    }

    .taskmodal-form {
        margin-right: 4rem;
    }
}
.profile {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    background-color: var(--secondary-color);
    margin: 0.5rem;
    border-radius: 16px;
    box-shadow: 0px 0px 5px;
}

.profile-buttons {
    height: 100%;
    padding: 1rem;
    min-height: 12rem;
}

.task-add {
    color: white;
    background: var(--primary-color);
    display: inline-block;
    padding: 1rem;
    text-align: center;
    border-radius: .5rem;
    cursor: pointer;
}

:root {
  --primary-color: #5746ea;
  --secondary-color: #1d204b;
  --background-color: #0d1323;
}

:root {
  background-color: #0d1323;
  background-color: var(--background-color);
  font-family: "Roboto";
}

.App {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  align-items: center;
  display: flex;
  justify-content: center;
}
.app-body {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
}

@media screen and (min-width: 600px) {
  .app-body {
    width: 80%;
    flex-direction: row;
    height: auto;
  }

}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

*{
	box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
